import * as THREE from "three";
import utils from "../../utils";

export function computeTetrahedron44(side = 1, rad = 0) {
  const R = side / Math.sqrt(3);
  const tau = 2.0 * Math.PI;
  const deg120 = tau / 3;
  const deg240 = 2 * deg120;

  const H = Math.sqrt(2/3) * R;
  const positions = [
    /* 0 */ R * Math.cos(rad + deg120), 0.0, R * Math.sin(rad + deg120), 
    /* 1 */ R * Math.cos(rad + deg240), 0.0, R * Math.sin(rad + deg240), 
    /* 2 */ R * Math.cos(rad), 0, R * Math.sin(rad),
    /* 3 */ 0.0, R, 0.0 
  ];

  const positions2 = [
    /* 0 */ R * Math.cos(rad + deg120), 0.0, R * Math.sin(rad + deg120), 
    /* 1 */ R * Math.cos(rad + deg240), 0.0, R * Math.sin(rad + deg240), 
    /* 2 */ R * Math.cos(rad), 0, R * Math.sin(rad),
    /* 3 */ 0.0, R, 0.0 
  ];

  const indices = [
    1, 3, 2, 
    1, 2, 0, 
    2, 3, 0, 
    1, 0, 3
  ];

  const normals = utils.calculateNormals(positions, indices);

  return {
    positions,
    indices,
    normals,
  };
}

export function computeTetrahedron58(side = 1, rad = 0) {
  const R = side / Math.sqrt(3);
  const tau = 2.0 * Math.PI;
  const deg120 = tau / 3;
  const deg240 = 2 * deg120;

  const positions = [
    /* 0 near */ R * Math.cos(rad + deg120), 0., R * Math.sin(rad + deg120), 
    /* 1 right */ R * Math.cos(rad + deg240), 0., R * Math.sin(rad + deg240), 
    /* 2 far */ R * Math.cos(rad), 0., R * Math.sin(rad),
    /* 3 top */ 0.0, R, 0.0,
    /* 4 bottom */ 0.0, -R, 0.0  
  ];

  const indices = [
    2, 4, 1, 
    1, 4, 0,
    0, 4, 2, 
    1, 3, 2,
    2, 3, 0, 
    3, 1, 0,
    1, 0, 2, // internal
    1, 2, 0, // internal
  ];

  const normals = utils.calculateNormals(positions, indices);

  return {
    positions,
    indices,
    normals,
  };
}

export function computeBipyramid(side = 1, rad = 0) {
  const R = side / Math.sqrt(3);
  const tau = 2.0 * Math.PI;
  const deg1 = tau / 4;
  const deg2 = 2 * deg1;

  const positions = [
    /* 0 */ R * Math.cos(rad + deg1), 0., R * Math.sin(rad + deg1), 
    /* 1 */ R * Math.cos(rad + deg2), 0., R * Math.sin(rad + deg2), 
    /* 2 */ R * Math.cos(rad + deg1), 0., R * Math.sin(rad + deg2),
    /* 3 */ R * Math.cos(rad + deg2), 0., R * Math.sin(rad + deg1),
    /* 4 */ -0.5 * R,  R, 0.5 * R,
    /* 5 */ -0.5 * R, -R, 0.5 * R,
  ];

  const indices = [
    // top
    4, 2, 1, 
    4, 1, 3,
    4, 0, 2,
    4, 3, 0,
    // bottom
    5, 1, 2, 
    5, 3, 1,
    5, 2, 0,
    5, 0, 3,
    // base
    0, 1, 2,
    1, 0, 3,
    0, 2, 1,
    1, 3, 0,
  ];

  const normals = utils.calculateNormals(positions, indices);

  return {
    positions,
    indices,
    normals,
    vertexCount: 6,
    traingles: 12
  };
}

export function computeCube(side = 1) {
  const s = side  * 0.5;

  const positions = [
    /* 0 */  s,  s,  s, 
    /* 1 */  s, -s,  s,
    /* 2 */  s,  s, -s,
    /* 3 */ -s, -s,  s,
    /* 4 */ -s,  s, -s,
    /* 5 */ -s,  s,  s,
    /* 6 */  s, -s, -s,
    /* 7 */ -s, -s, -s,
  ];

  const indices = [
    // 
    5, 3, 0, 
    3, 1, 0,
    //
    3, 7, 1,
    6, 1, 7,
    // 
    5, 0, 2, 
    5, 2, 4,
    // 
    0, 1, 2,
    1, 6, 2,
    // 
    2, 6, 7,
    7, 4, 2,
    // 
    3, 5, 4,
    7, 3, 4,
  ];

  const normals = utils.calculateNormals(positions, indices);

  return {
    positions,
    indices,
    normals,
    vertexCount: 8,
    traingles: 12
  };
}


function createGeometry(attributesData) {
  
  const geo = new THREE.BufferGeometry();
  geo.setIndex(attributesData.indices);
  geo.setAttribute(
    "position",
    new THREE.BufferAttribute(new Float32Array(attributesData.positions), 3)
  );

  geo.setAttribute(
    "normal",
    new THREE.BufferAttribute(new Float32Array(attributesData.normals), 3)
  );
  
  geo.setIndex(attributesData.indices);
  
  return geo;
}

export default createGeometry;