import * as THREE from 'three';

const col = new THREE.Color();

const setColor = (object) => (color) => { 
  if(typeof object.value !== 'undefined') {
    object.value = col.set(color);
  } else {
    object.color = col.set(color); 
  }
}

export function initSceneGui(gui, guiParams, objects) {
  const { 
    scene, 
    floor, 
    ambientLight, 
    directionalLight, 
    directionalLightHelper, 
    directionalLightCameraHelper,
    pointLight,
    pointLightHelper,
    pointLightCameraHelper,
    hemisphereLight,
    hemisphereLightHelper,
    rectAreaLight,
    rectAreaLight2,
    rectAreaLightHelper,
    rectAreaLightHelper2,
    spotLight,
    spotLight2,
    spotLight3,
    spotLightHelper,
  } = objects;
  const sceneGui = gui.addFolder("Scene");
  sceneGui.addColor(guiParams, "sceneBackground").name("background")
    .onChange(() => {
      scene.background = col.set(guiParams.sceneBackground)
    });
  sceneGui.add(guiParams, "fogEnabled").onChange((b) => {
    scene.fog = b ? new THREE.Fog(guiParams.fogColor, guiParams.fogNear, guiParams.fogFar) : null;
  });
  sceneGui.addColor(guiParams, "fogColor").name("fog")
    .onChange(() => {
      if(scene.fog) { scene.fog.color = col.set(guiParams.fogColor) }
    });
  sceneGui.add(guiParams, "fogNear", 1, 20, 0.5)
  .onChange(() => {
    if(scene.fog) { scene.fog = new THREE.Fog(guiParams.fogColor, guiParams.fogNear, guiParams.fogFar) }
  });
  sceneGui.add(guiParams, "fogFar", 20, 400, 0.5)
  .onChange(() => {
    if(scene.fog) { scene.fog = new THREE.Fog(guiParams.fogColor, guiParams.fogNear, guiParams.fogFar) }
  });
  
  const floorGui = gui.addFolder("Floor");
  floorGui.add(floor, "visible")
  floorGui.add(floor.position,"y", -20, 0, 1.0)
  floorGui.addColor(guiParams, "floorColor").name("color")
    .onChange(() => {
      floor.material.color = col.set(guiParams.floorColor);
    })
  floorGui.addColor(guiParams, "floorEmissiveColor").name("emissive")
  .onChange(() => {
    floor.material.emissive = col.set(guiParams.floorEmissiveColor);
  })
  floorGui.add(floor.material, "emissiveIntensity", 0, 2, 0.01)
  
  // lightGui.open();
  const ambient = gui.addFolder("Ambient")
  ambient.add(ambientLight, "visible");
  ambient.addColor(guiParams, "ambientColor")
    .onChange(setColor(ambientLight))
  ambient.add(ambientLight, "intensity", 0, 1, 0.005);
  
  const directional = gui.addFolder("Directional")
  directional.add(directionalLight, "visible").onChange((v) => {
    directionalLightHelper.visible = v;
    directionalLightCameraHelper.visible = v;
  
  })
  directional.add(directionalLightHelper, "visible").name('helper visible').listen()
  directional.add(directionalLightCameraHelper, "visible").name('shadow camera helper visible').listen()
  directional.addColor(guiParams, "directionalLightColor")
    .onChange(setColor(directionalLight));
  directional.add(directionalLight, "intensity", 0, 1, 0.01);
  
  const point = gui.addFolder("Point")
  point.add(pointLight, "visible").onChange((v) => {
    pointLightHelper.visible = v
    pointLightCameraHelper.visible = v;
  })
  point.add(pointLightHelper, "visible").name('helper visible').listen()
  point.add(pointLightCameraHelper, "visible").name('camera helper visible').listen()
  point.addColor(guiParams, "pointLightColor")
    .onChange(setColor(pointLight));
  point.add(pointLight, "intensity", 0, 10, 0.01);
  point.add(pointLight, "distance", 0, 100, 0.01);
  point.add(pointLight, "decay", 0, 10, 0.05);
  
  const hemisphere = gui.addFolder("Hemisphere")
  hemisphere.add(hemisphereLight, "visible")
  hemisphere.add(hemisphereLightHelper, "visible").name('helper visible')
  hemisphere.addColor(guiParams, "hemisphereSkyColor")
  .onChange(() => {
  
    hemisphereLight.color = col.set(guiParams.hemisphereSkyColor);
  });
  hemisphere.addColor(guiParams, "hemisphereGroundColor")
  .onChange(() => {
  
    hemisphereLight.groundColor = col.set(guiParams.hemisphereGroundColor);
  });;
  hemisphere.add(hemisphereLight, "intensity", 0, 10, 0.01);
  
  
  const spot = gui.addFolder("Spot")
  spot.add(spotLight, "visible")
  spot.add(spotLightHelper, "visible").name('helper visible')
  spot.addColor(guiParams, "spotLightColor")
    .onChange((c) => {
      spotLight.color = c;
      spotLight2.color = c;
      spotLight3.color = c;
      // spotLight.color = c;
      // setColor(spotLight)(c)
      // setColor(spotLight2)(c)
      // setColor(spotLight3)(c)
    });
  spot.add(spotLight, "intensity", 0, 10, 0.01);
  spot.add(spotLight, "distance", 0, 30, 0.01);
  spot.add(spotLight, "angle", 0, Math.PI, Math.PI/180);
  spot.add(spotLight, "penumbra", 0, 2, 0.01);
  spot.add(spotLight, "decay", 0, 1, 0.01);
  
  const rect = gui.addFolder("Rect")
  rect.add(rectAreaLight, "visible").onChange(v => {
    rectAreaLight2.visible = v;
  });
  rect.add(rectAreaLightHelper, "visible").name('helper visible')
  .onChange(v => {
    rectAreaLightHelper2.visible = v;
  });
  rect.addColor(guiParams, "rectAreaLightColor")
  .onChange((col) => {
    setColor(rectAreaLight)(col)
    setColor(rectAreaLight2)(col)
  });
  rect.add(rectAreaLight, "intensity", 0, 30, 0.01).onChange(v => {
    rectAreaLight2.intensity = v;
  });;;
  rect.add(rectAreaLight, "width", 0, 50, 0.5).onChange(v => {
    rectAreaLight2.width = v;
  });
  rect.add(rectAreaLight, "height", 0, 50, 0.5).onChange(v => {
    rectAreaLight2.height = v;
  });
  
}
