  // scene
  shadowMapType: THREE.PCFShadowMap,
  sceneBackground: "#010101",
  fogEnabled: true,
  fogColor: "#212123",
  fogNear: 4,
  fogFar: 400,
  // floor
  floorVisible: true,
  floorColor: "#fafafa",
  floorEmissiveColor: "#fefefe",
  floorEmissiveIntensity: 0.15,
  
  // lights
  ambientColor: '#fafafa',
  hemisphereGroundColor:'#fa0055',
  hemisphereSkyColor:'#fafafa',
  directionalLightColor: "#ffffff",
  dirLightIntensity: 0.45,
  pointLightColor: "#ffffff",
  pointLightIntensity: 8.0,
  pointLightDistance: 12,
  pointLightDecay: 2.45,
  rectAreaLightColor: "#ffffff",
  rectAreaLightColor2: "#ffffff", // "#ffee00",
  rectLight: {
    intensity: 5,
    width: 30,
    height: 3,
  },
  
  spotLightColor: "#ff40e4",
  spotLightIntensity: 10,
  spotLightDistance: 30,
  spotLightDecay: 0,
  spotLightAngle: 3.0,
  // tetrahedron
  animateMesh: false,
  frontColor:"#000000",
  backColor: "#050505",
  frontReflectivity: 100,
  frontMetalness: 0.95,
  frontRoughness: 0.05,
  backReflectivity: 1.0,
  backRoughness: 0.92,
