const canvas = document.getElementById("canvas-id") as HTMLCanvasElement;

const clock = new THREE.Clock();
const renderer = new THREE.WebGLRenderer({
  canvas,
  antialias: true,
});

stats = Stats();


renderer.sortObjects = false

renderer.setSize(window.innerWidth, window.innerHeight);
renderer.pixelRatio = window.devicePixelRatio;
renderer.shadowMap.enabled = true;
renderer.shadowMap.type = guiParams.shadowMapType;
renderer.toneMappingExposure = guiParams.exposure;

scene = new THREE.Scene();
scene.background = new THREE.Color(guiParams.sceneBackground);
scene.fog = new THREE.Fog(guiParams.fogColor, guiParams.fogNear, guiParams.fogFar);

const camera = new THREE.PerspectiveCamera(
  60,
  window.innerWidth / window.innerHeight,
  0.001,
  1000
);

new OrbitControls(camera, canvas);

camera.position.y = 3;
camera.position.z = 15;


floor = new THREE.Mesh(
  new THREE.BoxGeometry(100, 0.5, 100, 10, 10, 1),
  new THREE.MeshStandardMaterial({ 
    color: guiParams.floorColor,
    emissive: guiParams.floorEmissiveColor,
    emissiveIntensity: guiParams.floorEmissiveIntensity,
  })
)

floor.visible = guiParams.floorVisible;
floor.receiveShadow = true;
floor.position.y = -20;
scene.add(floor);



function initLights () {
  ambientLight = new THREE.AmbientLight(guiParams.ambientColor, 0.15);
  ambientLight.visible = false;
  scene.add(ambientLight);
  
  hemisphereLight = new THREE.HemisphereLight(
    guiParams.hemisphereSkyColor, 
    guiParams.hemisphereGroundColor,
    0.45
  );
    // hemisphereLight.castShadow = true;
    hemisphereLight.visible = false;
  
  scene.add(hemisphereLight)
  
  hemisphereLightHelper = new THREE.HemisphereLightHelper(hemisphereLight, 0.2);
  hemisphereLightHelper.visible = false;
  scene.add(hemisphereLightHelper);
  
  directionalLight = new THREE.DirectionalLight(guiParams.directionalLightColor, guiParams.dirLightIntensity);
  directionalLight.position.set(20, 24, 0);
  directionalLight.visible = true;
  // directionalLight.castShadow = true;
  directionalLight.shadow.camera.near = 1;
  directionalLight.shadow.camera.far = 50;
  directionalLight.shadow.radius = 10;
  
  scene.add(directionalLight);
  
  directionalLightHelper = new THREE.DirectionalLightHelper(directionalLight, 0.3)
  directionalLightHelper.visible = false;
  scene.add(directionalLightHelper);
  
  directionalLightCameraHelper = new THREE.CameraHelper(directionalLight.shadow.camera);
  directionalLightCameraHelper.visible = false;
  scene.add(directionalLightCameraHelper);
  
  pointLight = new THREE.PointLight(col.set(guiParams.pointLightColor), guiParams.pointLightIntensity, guiParams.pointLightDistance, guiParams.pointLightDecay);
  pointLight.position.set(10.0, 10.2, 1.5);
  pointLight.visible = true;
  pointLight.castShadow = true;
  pointLight.shadow.camera.near = 0.01;
  pointLight.shadow.camera.far = 1000;
  pointLight.shadow.radius = 10;
  scene.add(pointLight);
  pointLightHelper = new THREE.PointLightHelper(pointLight, 0.1, guiParams.pointLightColor);
  pointLightHelper.visible = false ;
  
  scene.add(pointLightHelper);
  pointLightCameraHelper = new THREE.CameraHelper(pointLight.shadow.camera);
  scene.add(pointLightCameraHelper);
  pointLightCameraHelper.visible = false;
  
  // works only with standard or physical material
  rectAreaLight = new THREE.RectAreaLight(col.set(guiParams.rectAreaLightColor), guiParams.rectLight.intensity,  guiParams.rectLight.width, guiParams.rectLight.height)
  rectAreaLight.position.set(-12.0, 2.0, 0.0);
  rectAreaLight.visible = true;
  // rectAreaLight.castShadow = true;
  rectAreaLight.lookAt(new THREE.Vector3())
  scene.add(rectAreaLight);
  
  rectAreaLightHelper = new RectAreaLightHelper(rectAreaLight);
  rectAreaLightHelper.visible = false;
  scene.add(rectAreaLightHelper);
  
  rectAreaLight2 = new THREE.RectAreaLight(col.set(guiParams.rectAreaLightColor2), guiParams.rectLight.intensity, guiParams.rectLight.width, guiParams.rectLight.height)
  rectAreaLight2.position.set(12.0, 2.0, 0.0);
  rectAreaLight2.visible = rectAreaLight.visible;
  rectAreaLight2.lookAt(new THREE.Vector3())
  scene.add(rectAreaLight2);
  
  rectAreaLightHelper2 = new RectAreaLightHelper(rectAreaLight2);
  rectAreaLightHelper2.visible = rectAreaLightHelper.visible;
  scene.add(rectAreaLightHelper2);


  spotLight = new THREE.SpotLight(
    col.set(guiParams.spotLightColor),
    guiParams.spotLightIntensity, 
    guiParams.spotLightDistance,
    guiParams.spotLightAngle,
    0.05,
    guiParams.spotLightDecay);
  spotLight.position.set(0, 25, 0);
  spotLight.visible = true;
  // spotLight.castShadow = true;
  spotLight.target.position.z = 0;
  scene.add(spotLight)
  scene.add(spotLight.target);
  
  spotLightHelper = new THREE.SpotLightHelper(spotLight);
  spotLightHelper.visible = false;
  scene.add(spotLightHelper);
  
  spotLight2 = new THREE.SpotLight(
    col.set(guiParams.spotLightColor),
    guiParams.spotLightIntensity, 
    guiParams.spotLightDistance,
    guiParams.spotLightAngle,
    0.05,
    guiParams.spotLightDecay)
  spotLight2.position.set(-10, -5, 10);
  spotLight2.visible = true;
  // spotLight2.castShadow = true;
  spotLight2.target.position.z = 0;
  scene.add(spotLight2)
  scene.add(spotLight2.target);
  
  spotLight3 = new THREE.SpotLight(
    col.set(guiParams.spotLightColor),
    guiParams.spotLightIntensity, 
    guiParams.spotLightDistance,
    guiParams.spotLightAngle,
    0.05,
    guiParams.spotLightDecay)
  spotLight3.position.set(10, -5, 10);
  spotLight3.visible = true;
  // spotLight3.castShadow = true;
  spotLight3.target.position.z = 0;
  scene.add(spotLight3)
  scene.add(spotLight3.target);
}

const geo1 = tetra(cube);
const geo2 = tetra(tetrahedron);


geo1.drawRange.count = guiParams.numberOfTriangles * 3;
// geo.translate(3, 0, -3)

const backMaterial = new THREE.MeshPhysicalMaterial( {
  map: null,
  color: col.set('#000000'),
  metalness: guiParams.backMetalness,
  roughness: guiParams.backRoughness,
  reflectivity: guiParams.backReflectivity,
  opacity: guiParams.backOpacity,
  side: THREE.BackSide,
  transparent: true,
  envMapIntensity: 4,
  premultipliedAlpha: true
  // TODO: Add custom blend mode that modulates background color by this materials color.
} );

const frontMaterial = new THREE.MeshPhysicalMaterial( {
  map: null,
  color: col.set('#000000'),
  metalness: guiParams.frontMetalness,
  roughness: guiParams.frontRoughness,
  reflectivity: guiParams.frontReflectivity,
  opacity: guiParams.frontOpacity,
  side: THREE.FrontSide,
  transparent: true,
  envMapIntensity: 10,
  premultipliedAlpha: true,

} );


const tetrahedronGroup = new THREE.Group();
geo2.scale(Math.sqrt(2),Math.sqrt(2),Math.sqrt(2));
tetrahedronGroup.add(new THREE.Mesh(geo2, backMaterial));
tetrahedronGroup.add(new THREE.Mesh(geo2, frontMaterial));
// tetrahedronGroup.translateZ(0.001);
// tetrahedronGroup.translateY(-Math.sqrt(13/8));
scene.add(tetrahedronGroup);
// backMaterial.color = col.set("#000000");
// frontMaterial.color = col.set("#000000");
const cubeGroup = new THREE.Group();
cubeGroup.add(new THREE.Mesh(geo1, backMaterial));
cubeGroup.add(new THREE.Mesh(geo1, frontMaterial));
scene.add(cubeGroup);


  stats.update(renderer.info)


tetraFront = tetrahedronGroup.children[1];
tetraFront.castShadow = true;
tetraBack = tetrahedronGroup.children[0];
tetraBack.castShadow = true;

camera.lookAt(tetrahedronGroup.position)

normalHelper = new VertexNormalsHelper( tetraBack, 2, 0xff0000 );
normalHelper.visible = guiParams.normalHelperVisible;
scene.add(normalHelper);
// clock.start();

function animate() {
  // time+= 0.01;
  const elapsed = clock.getElapsedTime();

  if(pointLight.visible) {
    pointLight.position.x =  Math.sin(elapsed * 0.25) * 2.0;
    pointLight.position.y =  Math.cos(elapsed * 0.75) * 5.0;
    pointLight.position.z =  -Math.cos(elapsed * 0.25)* 2.0;
  }

  // if(spotLight.visible) {
  //   spotLight.position.x =  -5 - Math.cos(elapsed * 0.25) * 6.0;
  //   spotLight.position.y =  4 + Math.sin(elapsed * 0.75) * 8.0;
  //   spotLight.position.z =  -Math.cos(elapsed * 0.25)* 4.0;
  //   spotLight.lookAt(tetrahedronGroup.position);
  //   spotLight2.position.x =  8 + Math.atan(elapsed * 0.65) * 6.0;
  //   spotLight2.position.y =  -2 - Math.cos(elapsed * 0.15) * 8.0;
  //   spotLight2.position.z =  -Math.cos(elapsed * 0.45)* 4.0;
  //   spotLight2.lookAt(tetrahedronGroup.position);
  //       spotLight3.position.x =  2 + Math.sin(elapsed * 0.15) * 6.0;
  //   spotLight3.position.y =  4 - Math.cos(elapsed * 0.8) * 8.0;
  //   spotLight3.position.z = 5 + -Math.atan(elapsed * 0.25)* 4.0;
  //   spotLight3.lookAt(tetrahedronGroup.position);
  // }

  if(guiParams.animate) {
    camera.position.x = 14 - Math.cos(elapsed * 0.5) * 7;
    camera.position.z = 20 + Math.sin(elapsed  * 0.4) * 10;
    camera.position.y = 14 + Math.sin(elapsed * 0.6) * 7;
    camera.lookAt(tetrahedronGroup.position);
    const sin1 = (Math.sin(elapsed + 0.1) + 1.0) * 0.1
    const sin2 = (Math.sin(elapsed + Math.PI/4) + 1.0) * 0.03
    const sin3 = (Math.sin(elapsed + 0.3) + 1.0) * 0.2
    spotLight.color = col.setHSL(sin1, .50, .50);
    spotLight2.color = col.setHSL(sin2, .50, .50);
    spotLight3.color = col.setHSL(sin3, .50, .50);
  }

if(guiParams.animateMesh) {
  // tetrahedron.rotateX(elapsed * 0.01);
  tetrahedronGroup.rotation.x =  elapsed * 0.2;
  tetrahedronGroup.rotation.y =  elapsed * 0.2;
  // tetrahedron.rotation.z =  elapsed;
  // tetrahedron.rotateZ(-delta);
  if(normalHelper.visible) normalHelper.update()
  
}
normalHelper.update()

if (clock.running) {
  renderer.render(scene, camera);
  requestAnimationFrame(animate);
  if(spotLightHelper.visible) spotLightHelper.update();
  // stats.update(renderer.info)
}
}

function initGui() {

  gui = new dat.GUI({ closeOnTop: true });

  const tetraGui = gui.addFolder("Mesh")
  tetraGui.open();
  tetraGui.add(guiParams, "animateMesh");
  tetraGui.add(guiParams, "normalHelperVisible").onChange((v) => {
normalHelper.visible = v
  });
  tetraGui.add(guiParams, "scale", 0.2, 4, 0.1).onChange(s => {
    tetrahedronGroup.scale.x = s
    tetrahedronGroup.scale.y = s
    tetrahedronGroup.scale.z = s
  });
  tetraGui.add(guiParams, "numberOfTriangles", 0, guiParams.numberOfTriangles, 1).onChange(s => {
    geo1.setDrawRange(0, s * 3)
  });
  const frontGui = tetraGui.addFolder("front");
  frontGui.add(tetraFront.material, "wireframe");
  frontGui.add(tetraFront.material, "transparent");
  frontGui.add(tetraFront.material, "reflectivity", 0, 100, 0.01);
  frontGui.add(tetraFront.material, "metalness", 0, 1, 0.01);
  frontGui.add(tetraFront.material, "roughness", 0, 1, 0.01);
  frontGui.add(tetraFront.material, "opacity", 0, 1, 0.01).onChange(s => {
    tetraFront.material.opacity = s
  });

  frontGui.addColor(guiParams, 'frontColor').onChange((c) => {
    tetraFront.material.color = col.set(c);
  });

  const backGui = tetraGui.addFolder("back");

  backGui.add(tetraBack.material, "wireframe");
  backGui.add(tetraBack.material, "transparent");
  backGui.add(tetraBack.material, "reflectivity", 0, 100, 0.01);
  backGui.add(tetraBack.material, "metalness", 0, 1, 0.01);
  backGui.add(tetraBack.material, "roughness", 0, 1, 0.01);
  backGui.add(tetraBack.material, "opacity", 0, 1, 0.01).onChange(s => {
    tetraBack.material.opacity = s
  });

  backGui.addColor(guiParams, 'backColor').onChange((c) => {
    tetraBack.material.color = col.set(c);
  });



  const objectTranslation = tetraGui.addFolder("translation");
  const objectRotation = tetraGui.addFolder("rotation");

  objectTranslation.add(guiParams, "translateX", -5, 5, 1.0).onChange(() => {
    tetrahedronGroup.position.x = guiParams.translateX;
    normalHelper.update()
    // mesh.translateX(guiParams.translateX);
  });
  objectTranslation.add(guiParams, "translateY", -5, 5, 1.0).onChange(() => {
    tetrahedronGroup.position.y = guiParams.translateY;
    normalHelper.update()
    // for annimation
    // mesh.translateY(guiParams.translateY);
  });
  objectTranslation.add(guiParams, "translateZ", -5, 5, 1.0).onChange(() => {
    tetrahedronGroup.position.z = guiParams.translateZ;
    normalHelper.update()
    // mesh.translateZ(guiParams.translateZ);
  });
  
  objectRotation.add(guiParams, "rotateX", 0, 360, 1).onChange(() => {
    tetrahedronGroup.rotation.x = (guiParams.rotateX * Math.PI) / 180;
    normalHelper.update()
  });
  objectRotation.add(guiParams, "rotateY", 0, 360, 1).onChange(() => {
    tetrahedronGroup.rotation.y = (guiParams.rotateY * Math.PI) / 180;
    normalHelper.update()
    // mesh.rotateY(guiParams.rotateY* Math.PI/180);
  });
  objectRotation.add(guiParams, "rotateZ", 0, 360, 1).onChange(() => {
    tetrahedronGroup.rotation.z = (guiParams.rotateZ * Math.PI) / 180;
    normalHelper.update()
    // mesh.rotateZ(guiParams.rotateZ* Math.PI/180);
  });

  const cameraGui = gui.addFolder("Camera");
  cameraGui.add(guiParams, 'animate');
  cameraGui.add(camera.position, "x", -50, 50, 1.0).listen();
  cameraGui.add(camera.position, "y", -50, 50, 1.0).listen();
  cameraGui.add(camera.position, "z", 0, 50, 1.0).listen();
  cameraGui.add(camera, "fov", 5, 180, 5);

}

if (module  && module.hot) {
  // module.hot.accept((a, b) => {
  //   // For some reason having this function here makes dat gui work correctly
  //   // when using hot module replacement
  // });
  module.hot.dispose(() => {
    // if (myApp) myApp.dispose();
    scene.clear()
    renderer.dispose();
    if(gui) {
      gui.destroy()
      gui = null;
      stats = null;
    }
  });
}

initLights();

animate()

initGui();

initSceneGui(gui, guiParams, {
    scene, 
    floor, 
    ambientLight, 
    directionalLight, 
    directionalLightHelper, 
    directionalLightCameraHelper,
    pointLight,
    pointLightHelper,
    pointLightCameraHelper,
    hemisphereLight,
    hemisphereLightHelper,
    rectAreaLight,
    rectAreaLight2,
    rectAreaLightHelper,
    rectAreaLightHelper2,
    spotLight,
    spotLight2,
    spotLight3,
    spotLightHelper,
})
